.music {
    #music-control {
        display: block;
        transform: scale(1);
        bottom: 30px;
        top:auto;
        right: 0px;
        width: 100%;
        height: 80px;
        opacity: 100%;
        transition: right 1s ease-in, bottom 1s ease-in, opacity 1s ease-in;

        &.touched {
            display: block;
            transform: scale(1);
            bottom: 30px;
            top:auto;
            right: 0px;
            width: 100%;
            height: 80px;
        }
    }
}

#music-control {
    bottom: 30px;
    width: 100%;
    opacity: 0%;
    right: 0;
    pointer-events: none;
    position: fixed;
    display: block;
    transition: right 1s ease-in, bottom 1s ease-in, width 1s ease-in-out, transform 1s ease-in-out, opacity 1s ease-in-out;
    z-index: 0;
}

.music .touched .music-scale, .music .touched.music-scale {  
    transform: scale(1.0);
    transition: 1s ease-in;
}

.touched .music-scale, .touched.music-scale {
    transform: scale(0.6);
    transition: 1s ease-in;
}

#music-control.touched {

    pointer-events: all;
    opacity: 100%;

    display: block;
    right: 0px;
    bottom: calc(100% - 60px);
    width: 100%;
    transition: right 1s ease-in, bottom 1s ease-in, width 1s ease-in-out, transform 1s ease-in-out, opacity 1s ease-in-out;
}

#record {
    display: block;
    position: fixed;

    transition: right 1.3s ease-in, bottom 1.3s ease-in, transform 1.3s ease-in;
    right: -600px;
    bottom: -600px;
    z-index: -4;
}

#record-outer {
    box-shadow: desaturate(darken($theme-2-primary, 10%), 40%) 0px 0px 80px;
    width: 340px;
    height: 340px;
    background-color: rgb(10, 10, 10);
    border-radius: 1000px;
    border: 120px solid rgb(4, 4, 4);
    animation: rotate linear 5s infinite;

    #image {
        position: absolute;
        width: 250px;
        height: 250px;
        border-radius: 150px;
        left: 50%;
        top: 50%;
        filter:brightness(100%) /*grayscale(100%)*/;
        transform: translateX(-50%) translateY(-50%);
    }
    
    &::after {
        content: '';
        background-color: black;
        height: 10px;
        width: 10px;
        border-radius: 10px;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translateX(-50%) translateY(-50%);
    }
}

.music #record.touched {
    &.play {
        transition: right 1s ease-out, bottom 1s ease-out, transform 1s ease-out;
        right: -254px;
        bottom: -154px;
    }
    transition: right 1.3s ease-in, bottom 1.3s ease-in, transform 1.3s ease-in;
    right: -600px;
    bottom: -600px;
}

#record.touched {
    transition: right 1.3s ease-in, bottom 1.3s ease-in, transform 1.3s ease-in;
    right: -550px;
    bottom: 100%;
    &.play {
        transition: right 1s ease-out, bottom 1s ease-out, transform 1s ease-out;
        right: -300px;
        bottom: calc(100% - 300px);
    }
}

@keyframes rotate {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

#next, #prev {
    cursor: pointer;
    pointer-events: all;
    margin: auto 0px auto 0px;
    opacity: 0%;
    display: block;
    transition: opacity 0.3s ease-in;
    height: 0px;
    width: 0px; 

    border-width: 0px;
    border-style: solid;
    border-color: transparent desaturate(darken($theme-2-primary, 10%), 20%) transparent transparent;
    transition: background-color 0.2s ease-in,
    height 0.2s ease-in,
    transform 0.2s ease-in,
    opacity 1s ease-in,
    border-width 0.2s ease-in,
    border-color 0.2s ease-in,
    margin 0.8s ease-in;
}

.light #next, .light #prev {
    border-color: transparent desaturate(darken($itheme-2-primary, 10%), 20%) transparent transparent;
}

#next {
    transform: scaleX(-1);
}

.music {
    // There is 800% a better way to do this
    #next {
        opacity: 100%;
        transform: scaleX(-1);
        border-width: 20px 34.64px 20px 0px;
        margin: auto 40px auto 40px;

        &::after{
            content: '';
            display: block;
            height: 0px;
            width: 0px; 
            margin-top: auto;
            margin-bottom: auto;

            border-style: solid;
            border-width: 20px 34.64px 20px 0px;
            border-color: transparent var(--theme-2-primary) transparent transparent;

            transform: translateY(-20px);

            transition: border-color 0.2s ease-in;
        }

        &:hover{
            height: 8px;
            transform: translateY(-4px) scaleX(-1);
        }
    }

    #prev {
        opacity: 100%;
        border-width: 20px 34.64px 20px 0px;
        margin: auto 40px auto 40px;

        &::after{
            content: '';
            display: block;
            height: 0px;
            width: 0px; 
            margin-top: auto;
            margin-bottom: auto;

            border-style: solid;
            border-width: 20px 34.64px 20px 0px;
            border-color: transparent var(--theme-2-primary) transparent transparent;

            transform: translateY(-20px);

            transition: border-color 0.2s ease-in;
        }

        &:hover{
            height: 8px;
            transform: translateY(-4px);
        }
    }
}

#playpause-control {
    cursor: pointer;
    flex: 0 0 auto;
    display: block;
    position: relative;
    background-color: var(--theme-2-primary);
    width: 60px;
    height: 60px;
    border-radius: 30px;
    box-shadow: desaturate(darken($theme-2-primary, 10%), 20%) 0px 0px;
    transition: background-color 0.2s ease-in, box-shadow 0.2s ease-in, transform 0.2s ease-in;
    pointer-events: all;
    transform: translateY(0px);

    &:hover{
        box-shadow: desaturate(darken($theme-2-primary, 10%), 20%) 0px 8px;
        transform: translateY(-8px);
        transition: box-shadow 0.2s ease-in, transform 0.2s ease-in;
    }

    .playpause {
        position: relative;
        left: 50%;
        top: 50%;
        width: 10px;
        height: 10px;
        border-style: solid;
        border-width: 8px 8px 8px 8px;
        border-color: desaturate(darken($theme-2-primary, 10%), 20%);
        transform: translateX(-50%) translateY(-50%);
        transition: border-color 0.2s ease-in, border-width 0.2s ease-in, width 0.2s ease-in, height 0.2s ease-in;
        
        &.play {
            width: 0px;
            height: 0px;
            border-width: 12px 0px 12px 24px;
            border-color: transparent desaturate(darken($theme-2-primary, 10%), 20%) transparent desaturate(darken($theme-2-primary, 10%), 20%);
            transition: border-color 0.2s ease-in, border-width 0.2s ease-in, width 0.2s ease-in, height 0.2s ease-in;
        }
    }
}

