@keyframes wiggle-float-l
{
    from{
        transform:translate(0,0px) rotate(-5deg);
        text-shadow: rgba(129, 129, 129, 0.5) 0px 0px,
        rgba(129, 129, 129, 0.2) 0px 0px;
    }
    25%{
        text-shadow: rgba(129, 129, 129, 0.5) 0px -4px,
        rgba(129, 129, 129, 0.2) 0px -8px;
    }
    55%{
        transform:translate(0,30px) rotate(5deg);
    }
    75%{
        text-shadow: rgba(129, 129, 129, 0.5) 0px 4px,
        rgba(129, 129, 129, 0.2) 0px 8px;
    }
    to {
        transform:translate(0,0px) rotate(-5deg);
        text-shadow: rgba(129, 129, 129, 0.5) 0px 0px,
        rgba(129, 129, 129, 0.2) 0px 0px;
    }
}

@keyframes wiggle-float-r
{
    from{
        transform:translate(0,0px) rotate(5deg);
        text-shadow: rgba(129, 129, 129, 0.5) 0px 0px,
        rgba(129, 129, 129, 0.2) 0px 0px;
    }
    25%{
        text-shadow: rgba(129, 129, 129, 0.5) 0px -4px,
        rgba(129, 129, 129, 0.2) 0px -8px;
    }
    55%{
        transform:translate(0,30px) rotate(-5deg);
    }
    75%{
        text-shadow: rgba(129, 129, 129, 0.5) 0px 4px,
        rgba(129, 129, 129, 0.2) 0px 8px;
    }
    to {
        transform:translate(0,0px) rotate(5deg);
        text-shadow: rgba(129, 129, 129, 0.5) 0px 0px,
        rgba(129, 129, 129, 0.2) 0px 0px;
    }
}